import React from "react"
import { Box, IconButton, lighten } from "@rp/react/web/mui/core"
import { Close } from "@rp/react/web/mui/icons"

type ClosePaneButtonProps = {
  close: () => void
}

function ClosePaneButton({ close }: ClosePaneButtonProps) {
  return (
    <IconButton data-testid="close-button" onClick={close} sx={{ p: 0.125, ml: 0.375 }}>
      <Box
        sx={theme => {
          return {
            width: 30,
            height: 30,
            backgroundColor: theme.palette.elevation.dp01,
            borderRadius: "8px",
            border: `1px solid ${theme.palette.outline.border}`,
            "&:hover": {
              backgroundColor: lighten(theme.palette.elevation.dp01, 0.1),
              color: theme.palette.text.primary,
            },
            color: theme.palette.text.secondary,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }
        }}
      >
        <Close sx={{ width: 20, height: 20 }} />
      </Box>
    </IconButton>
  )
}

export { ClosePaneButton }
